.Socials {
  margin-top: 10%;
}

.Socials__linkedin {
  display: flex;
  margin-top: -120px;
  margin-left: 500px;
  padding-right: 150px;
}
.Socials__linkedinlogo {
  height: 200px;
  margin-left: 10%;
}

.Socials__linkedinlogo:hover {
  transform: scale(1.15);
  transition-duration: 0.5s;
  cursor: pointer;
}

.Socials__githublogo {
  display: flex;
  height: 190px;
  margin-left: 20%;
}

.Socials__githublogo:hover {
  transform: scale(1.15);
  transition-duration: 0.5s;
  cursor: pointer;
}

.Socials__button:hover {
  background-color: red;
  cursor: pointer;
}
