.downloadCV {
  display: flex;
  align-items: center;
}
.downloadCV__button {
  margin-top: 5%;
  margin-left: 43%;
  display: flex;
  color: #fff;
  border: 2px solid #222c3c;
  border-radius: 0px;
  padding: 18px 36px;
  display: inline-block;
  font-family: "BebasNeueRegular";
  font-size: 30px;
  letter-spacing: 1px;
  cursor: pointer;
  box-shadow: inset 0 0 0 0 #222c3c;
  -webkit-transition: ease-out 0.4s;
  -moz-transition: ease-out 0.4s;
  transition: ease-out 0.4s;
}

.downloadCV__button:hover {
  box-shadow: inset 400px 0 0 0 #222c3c;
}
