.Intro{
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.Intro__card{
    display: flex;
    flex-direction: column;
    margin: 10% auto;
    width: 65vw;
    height: 60vh;
    border-radius: 40px;
    background-color: white;
    box-shadow: 5px 5px 30px 7px rgba(0,0,0,0.25), -5px -5px 30px 7px rgba(0,0,0,0.22);
    transition: 0.2s;
    z-index: 9;
    
}


.Intro__profilepic{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin-top: 30px;
    align-self: center;
    box-shadow: 5px 5px 30px 7px rgba(0,0,0,0.25), -5px -5px 30px 7px rgba(0,0,0,0.22);
    overflow: hidden;
    display:inline-block;
    vertical-align:middle;
}

.Intro__written{
    display: flex;
    justify-content: center;
    margin: 2.5rem;
}